.line-through {
  text-decoration: line-through;
  user-select: none; }

.myAccount {
  text-align: center;
  margin: 50px 0 75px 0; }
  .myAccount h2 {
    font-size: 4.5em;
    font-family: Roboto Slab, serif;
    margin-bottom: 0.27778em;
    color: #FF0000;
    line-height: 1.2em; }
  .myAccount > p {
    font-size: 1.6em;
    font-family: Roboto Mono, monospace;
    margin-bottom: 3.125em;
    color: #6B6A6A;
    line-height: 1.3em; }
  .myAccount section {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-gap: 30px; }
  .myAccount form {
    text-align: center; }
  .myAccount legend {
    display: inline-block;
    font-size: 2.2em;
    font-family: Roboto Mono, monospace;
    margin-bottom: 1.13636em; }
  .myAccount label {
    display: inline-block;
    font-size: 1.8em;
    font-family: Roboto Mono, monospace;
    margin-bottom: 0.34722em;
    font-weight: 300; }
  .myAccount input {
    display: inline-block;
    width: 100%;
    border: 1px solid #979797;
    min-height: 50px;
    padding: 10px;
    font-size: 1.6em;
    font-family: Roboto Mono, monospace;
    margin-bottom: 0; }
  .myAccount input:not([type='submit']), .myAccount textarea {
    margin-bottom: 20px; }
  .myAccount input[type='submit'] {
    font-size: 1.8em;
    font-family: Roboto Mono, monospace;
    margin-bottom: 0;
    color: white;
    background: #DA542F;
    margin-top: 20px; }
  .myAccount .error {
    color: #FF0000; }
